.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f5f5f5;
  background-image: linear-gradient(to bottom, #f5f5f5, #a2dcea);
  min-height: 100vh;
  padding-top: 2rem;
}

.pet-details-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;

  width: 100%;
  max-width: 1200px;
  margin: auto;
  flex-wrap: wrap;
}
.component-details {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  background-color: #f5f5f5;
  padding: 16px;
  border-radius: 8px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
  max-width: 1200px;
  width: 80%;
  margin: auto;
  flex-wrap: wrap;
}
.contact-modal-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  width: 100%;
  max-width: 1200px;
  margin: 16px;
  flex-wrap: wrap;
}

.similar-pets-section {
  width: 100%;
  max-width: 1200px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  margin: 16px;
  padding-right: 80px;
}

.pet-details-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-color: #f5f5f5;
}

.pet-image-section {
  flex: 1;
  max-width: 50%;
  padding-right: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pet-details-img {
  max-width: 100%;
  display: block;
  margin: 5rem auto;
  border-radius: 8px; /* increased border-radius */
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3); /* more prominent box-shadow */
  border: 2px solid #4169e1; /* added border */
}
.pet-info-section {
  margin-right: 2rem;
}
.pet-details-header {
  font-size: 24px;
  font-weight: bold;
  margin-top: 16px;
  margin-bottom: 8px;
}

.breed-age {
  font-size: 20px;
  margin-bottom: 16px;
}

.pet-details-para {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 16px;
}
/* button styling  */
.adopt-button {
  padding: 1rem 1.5rem;
  border-radius: 10px;

  margin: 0 auto; /* Center the button horizontally */
  border: none;
  background-color: #4169e1;
  color: #f8fafb;
  cursor: pointer;
  font-size: clamp(1rem, 2vw, 1.5rem);
  transition: 0.2s ease-in-out;
  text-align: center;
}

.adopt-button:hover {
  background-color: rgb(195, 229, 243);
  color: #4169e1f4;
  outline: #4169e1f4;
  outline-width: 3px;
  outline-style: solid;
}
/* list styles horizontal*/
.pet-details-list {
  margin: 2rem 0 2rem 0;
  list-style-type: none;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 8px;
}

.pet-details-li {
  font-size: 18px;
  line-height: 1.5;
  color: #555;
}

.pet-details-li:not(:last-child):after {
  content: "";
  margin: 0 1rem;
  color: #ccc;
  vertical-align: middle;
}

.pet-details-about {
  background-color: #f8f8f8;
  border-radius: 8px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  margin: 16px;
  padding: 24px;
  text-align: left;
  width: 100%;
  max-width: 600px;
}

.pet-details-about-heading {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 16px;
  color: #044293;
}

.pet-details-about-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.pet-details-about-li {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 8px;
  color: #555;
}

.pet-details-about-fee {
  font-size: 18px;
  font-weight: bold;
  margin-top: 16px;
}

.adopt-button {
  display: block;
  margin: 1.5rem auto;
  padding: 0.8rem 1.2rem;
  border-radius: 10px;
  margin: 1rem auto;
  border: none;
  background-color: #4169e1;

  color: #f8fafb;
  cursor: pointer;
  font-size: clamp(1rem, 2vw, 1.5rem);
  transition: 0.2s ease-in-out;
  text-align: center;
  margin-top: 1.5rem;
}

.adopt-button:hover {
  background-color: rgb(195, 229, 243);
  color: #4169e1;
  outline: #4169e1;
  outline-width: 3px;
  outline-style: solid;
}

.similar-pets-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 24px;
  text-align: center;
}

.similar-pets-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
}

.similar-pet {
  background-color: #f8f8f8;
  border-radius: 8px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
  margin: 8px;
  padding: 16px;
  text-align: left;
  width: 100%;
  max-width: 600px;
  text-decoration: none;
}

.similar-pet-img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-radius: 4px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
  margin-bottom: 16px;
  max-width: 100%;
  margin: 1rem auto;
  border-radius: 8px; /* increased border-radius */
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3); /* more prominent box-shadow */
  border: 2px solid #4169e1; /* added border */
}

.similar-pet-name {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 8px;
  color: #333;
}

.similar-pet-breed {
  font-size: 16px;
  color: #666;
  margin-bottom: 16px;
}
