  /* filters Container for find pets page */
   .filtersContainer {
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    border-radius: 16px;
    border-style: solid;
    border-width: 1px;
    border-color: rgba(2, 104, 144, 0.8);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    align-items: center;
    justify-content: flex-start;
    max-width: 900px;
    height: fit-content;
    margin-top: 10px;
  }

  .filterAndCards{
    display: flex;
    flex-direction: row;
  }
  
  .pageButtonsContainer{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-left: 290PX;
  }

  .find-pet-select {
    font-weight: lighter;
    color: #333;
    margin-bottom: 8px;
    font-size: x-large;
    align-self: start;
  }
  
   .filter-select {
    padding: 8px;
    border-radius: 4px;
    border: 2px solid #a3daff;
    background-color: #fff;
    color: #333;
    font-size: 16px;
    min-width: 100px;
    max-width: 200px;
    appearance: none;
  }
  
   .loadingScreen{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    background-color: #edf9fd;
  }

  .loadingText{
    font-weight: lighter;
    color: #333;
    margin-bottom: 8px;
    font-size: xxx-large;
  }
  
  .filter-button {
    background-color: #4db6ac;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.3s;
    margin-top: 28px;
  }
  
  /* .filter-button:hover {
    background-color: #26a69a;
    transform: scale(1.05);
  } */
  
  /* add styles to prevent overlapping */
   @media (max-width: 768px) {
    .search-container {
      margin-top: 2rem;
    }
  
    .search-for-animal {
      padding: 20px;
    }
  }
  
  .petcardarea {
    border-bottom: 1px solid black;
    border-top: 1px solid black;
  }
  
  .petCardDisplay{ 
    display: flex;
  }
  
  /* Media Query if screen is bigger than 700 px */
  
  .loading{
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-top: 250px;
    padding-left: 350px;
    width: 100%;
    
  }
  
