.hero-section {
  grid-column-start: 1;
  grid-column-end: 3;
  height: 70vh;
  width: 100vw;
  display: flex;
  margin-top: 0%;
  flex-direction: column;
  justify-content: left;
  align-items: left;
  position: relative;
  padding: 0rem 0rem 0rem 0rem;
  background-image: linear-gradient(
    #a2dcea,
    #a2dcea4b,
    #a2dcea00,
    #b4e6f300,
    #b4e6f300,
    #b4e6f300,
    #98d5e16e,
    #98d5e100
  );
}


.hero-section img {
  position: absolute;
  transform: scaleX(-1);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  object-fit:cover;
  /* object-position: -40% 5%; */
  filter: saturate(1.2);
  border-radius: 0;
  z-index: -1;
}

.hero-section-content-wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: right;
  align-items: right;
  padding-right: 10%;
