/* PetCard */
.petcardcontainer {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 15px;
  width: 100%;
  max-width: 100%;
  border-radius: 5%;
}

.pet-card {
  display: flex;
  flex-direction: column;
  margin: 0rem 2rem 2rem 1.5rem;
  align-items: center;
  justify-content: center;
  padding: 20px;
  border-radius: 10px;
  border-top: 2px solid rgba(255, 255, 255, 0.876);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(0, 180, 200, 0.03);
  border-bottom: 2px solid rgb(0, 222, 247);
  box-shadow: 10px 10px 20px rgba(2, 104, 144, 0.8);
  background-color: rgb(255, 255, 255);
  transition: 0.2s ease-in-out;
  width: 100%;
  max-width: 300px;
  max-height: 400px;
}

.pet-card:hover {
  transform: translateY(-5px);
  box-shadow: 35px 30px 30px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.pet-card:hover .See-Pet-Profile {
  transform: translateY(0px);
  opacity: 1;
  cursor: pointer;
}


.pet-card__image {
  width: 100%;
  height: 300px;
  border-radius: 10px;
  overflow: hidden;
}

.pet-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.pet-card__details {
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  text-align: left;
  width: 100%;
  margin-top: 10px;
  color: #044293;
  /* color: rgb(0, 0, 0); */
  background-image: linear-gradient(
    #f7feff00,
    #03fefa00,
    #0dc9da00,
    #24b0d300,
    #03fefa06,
    #076df204,
    #076df200,
    #076df200
  );
}

.pet-card__details h1 {
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  margin-bottom: 15px;
  text-align: left;
  width: 100%;
  color: #044293;
  position: relative;
  max-height: 180px;
}

.pet-card__name {
  font-size: 24px;
  font-weight: bold;
  margin: 0;
}

.pet-card__breed {
  font-size: 16px;
  margin: 0;
}

.pet-card__gender {
  font-size: 16px;
  margin: 0;
}

.pet-card__location {
  font-size: 16px;
  margin: 0;
}

.See-Pet-Profile {
  width: 190px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: absolute;
  margin-top: 0px;
  margin-left: 13%;
  padding-top: 0px;
  padding-bottom: 4px;
  top: 40%;
  font-size: 27px;
  border: 3px solid #044293;
  /* background-image: white; */
  background-image: linear-gradient(
    #fbfdfd,
    #fcfcfc,
    #fcfffff5,
    #fdfffff9,
    #fffffff5
  );
  border-radius: 25px;
  transition: 0.5s;
  color: #174cea;
  transform: translateY(20px);
  opacity: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.loading {
  display: block;
  justify-content: center;
